body {
  margin: 0;
  font-family: Girassol, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: antiquewhite;
  background-image: url(https://images.unsplash.com/photo-1563340012-9a46fb6a29ff?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2850&q=80);
  background-size: 105% auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn {
  font-family: Girassol;
  color: antiquewhite;  
}

.addbtn {
  font-family: Girassol;
  color: antiquewhite;
  background-color: #795D37;
  border-radius: 50%;
  width: 70px;
  height: 70px;  
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
  }

.addbtn:hover {
  background-color: #D4B483;
  box-shadow: 0px 15px 20px rgba(250, 235, 215, 0.5);
  color: #fff;
  transform: translateY(-7px);
  }

.subbtn {
  font-family: Girassol;
  color: antiquewhite;
  background-color: #795D37;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
}

.subbtn:hover {
  background-color: #D4B483;
  box-shadow: 0px 15px 20px rgba(250, 235, 215, 0.5);
  color: #fff;
  transform: translateY(7px);
}

.ownbtn {
  font-family: Girassol;
  color: antiquewhite;
  background-color: #795D37;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
}

.ownbtn:hover {
  background-color: #D4B483;
  box-shadow: 0px 15px 20px rgba(250, 235, 215, 0.5);
  color: #fff;
  transform: scale(1.1);
}

.inputbox {
  font-family: Girassol;
  color: antiquewhite;
  background-color: #795D37;
  height: 30px;
  margin-bottom: 21px;
  margin-top: 13px;
  text-align: center;
}

.resetbtn {
  font-family: Girassol;
  color: antiquewhite;
  background-color: #795D37;
  border-radius: 50%;
  width: 70px;
  height: 30px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
  margin-top: 55px;
  margin-left: -60px;
}

.resetbtn:hover {
  background-color: #D4B483;
  box-shadow: 0px 15px 20px rgba(250, 235, 215, 0.5);
  color: #fff;
  transform: scale(0.9);
}

::placeholder {
  color: antiquewhite;
}

.container {
  padding: 50px;
  display: grid;
  grid-template-columns: 130px;
}

.input {
  grid-column: 2/2;
  grid-row: 2/2;
  /* padding-top: 33px; */
}

.updown {
  grid-column: 2/1;
}

h1 {
  padding-left: 100px;
}

.mult {
  margin-top: 100px;
  grid-column: 4/1;
}

.multbtn {
  font-family: Girassol;
  color: antiquewhite;
  background-color: #795D37;
  border-radius: 50%;
  width: 70px;
  height: 30px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
}

.multbtn:hover {
  background-color: #D4B483;
  box-shadow: 0px 15px 20px rgba(250, 235, 215, 0.5);
  color: #fff;
  transform: scale(0.9);
}

.multbox {
  font-family: Girassol;
  color: antiquewhite;
  background-color: #795D37;
  height: 30px;
  width: 65px;
  text-align: center;
  /* margin-bottom: 21px;
  margin-top: 13px; */
}
